html {
    font-family: Arial, Helvetica, sans-serif;
  }
  
  .container {
    background-color: rgb(10, 27, 184);
    max-width: 600px;
    padding: 10px 20px 20px;
    border-radius: 12px;
    margin: 0 auto;
  }
  
  h1 {
    text-align: center;
  }
  
  .container h2 {
    margin: 0 auto;
    text-align: center;
    padding: 10px 0 20px 0;
    color: white;
  }
  
  button {
    background: rgb(211, 216, 253);
    color: rgb(10, 27, 184);
    border: 2px solid rgb(10, 27, 184);
    border-radius: 5px;
    font-size: 20px;
  }
  
  section div,
  section p {
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    margin-top: 0px;
  }
  
  section p {
    padding-bottom: 20px;
  }
  
  section > div.closed {
    margin-bottom: 20px;
  }
  